// 2-thumb-over ==========================================================================

.thumb-over{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}
.thumb-over-hover, .thumb-over-dark{
  overflow: hidden;
  background: rgba(0,0,0,0.5);
}
.thumb-over-hover, .thumb-over-hover>*{
  opacity: 0;
  -webkit-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
.thumb-over-hover>*{
  -ms-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.thumb-over-hover.thumb-over-hover-in>*{
  -ms-transform: scale(1.2);
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.thumb-over-hover.thumb-over-hover-out>*{
  -ms-transform: scale(0.8);
  -webkit-transform: scale(0.8);
  transform: scale(0.8);
}
.thumb-over-hover:hover, .thumb-over-hover:hover>*{
  opacity: 1;
}
.thumb-over-hover:hover>*{
  -ms-transform: scale(1);
  -webkit-transform: scale(1);
  transform: scale(1);
}



.thumb-over-corner-tr, .thumb-over-corner-tl, .thumb-over-corner-br, .thumb-over-corner-bl, .thumb-over-center-top, .thumb-over-center-mid, .thumb-over-center-bottom{
