// 3-Parallax ==========================================================================

.fixed_bg {
  background-size: cover;
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-color: #222;
}
.banner {
  overflow: hidden;
  width: 100%;
  position: relative;
  clear: both;
}
.image-banner{
  background: center 0 no-repeat ;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='path/to/image/1.jpg',sizingMethod='scale');
  -ms-filter: "progid:DXImageTransform.Microsoft.AlphaImageLoader(src='path/to/image/1.jpg',sizingMethod='scale')";
}


// Video Background
.video-banner {
  background-color:#000;
}
.video-banner video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translateX(-50%) translateY(-50%);
  background-size: cover;
  transition: 1s opacity;
  opacity: 0.6;
}
.video-banner video.stopfade{
  opacity: 0.2;
}

.banner-overlay:after{
  content:'';
  background-color:#000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
}



