// 4-Modals ==========================================================================


.modal{
  z-index: 999999;
  padding: 0 !important;
  -webkit-overflow-scrolling: touch;
}
.modal .modal-dialog{
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  top: 0;
  width: 100%;
  left: 0;
  right: 0;
  padding: 0 $main-space;
}
.modal-xs.modal .modal-dialog {
  max-width: 350px;
}

.modal-content {
  box-shadow: rgba(0,0,0,0.1) 0 0 100px;
  border: 0;
  @include transition( all 0.3s ease-in-out);
}
.modal.modal-top .modal-dialog{
  align-items: flex-start;
  padding:0;
}

.modal-body{
  padding: 0 !important;
}
.modal-header{
  position: relative;
  border-bottom: 0;
  padding: $main-space;
}
.modal-header h2{
  font-weight: 600;
  font-size: 20px;
  margin: 0;
}

.modal-backdrop {
  background-color: #333;
  z-index: 99999;
}
.modal-backdrop.in {
  filter: alpha(opacity=80);
  opacity: .8;
}

.modal .modal-footer{
  display: block;
  padding:0 !important;
}
.modal-footer>:not(:first-child){
  margin-left: 0;
}

// modals types
.modal.fade .modal-dialog {
  -webkit-transform: translate(0,2%);
  transform: translate(0,2%);
}
.modal.fade.modal-top .modal-dialog {
  padding: $main-space;
  -webkit-transform: translate(0,-2%);
  transform: translate(0,-2%);
}
.modal.fade.modal-full-sm .modal-dialog, .modal.fade.modal-full .modal-dialog {
  padding: 0;
  -webkit-transform: translate(3%, 0);
  transform: translate(3%, 0);
}
.modal.fade.show .modal-dialog {
  -webkit-transform: translate(0);
  transform: translate(0);
}
.modal.modal-center .modal-content{
  @include transform(scale(.9));
}
.modal.modal-center.show .modal-content{
  @include transform(scale(1));
}

.modal.fade.modal-full .modal-dialog{
  background: #fff;
  max-width: none;
}
.modal.fade.modal-full .modal-content {
  height: 100%;
  box-shadow: none;
}

.modal-full .modal-inner{
  width: 100%;
  margin: 0 auto;
  padding: 2rem $main-space;
  background: #fff;
}
.modal-full-sm .modal-inner{
  margin:2rem $main-space;
}
.modal-full .modal-inner-sm{
  max-width: 450px;
}

.modal.modal-side-md .modal-dialog{
  left: auto;
  margin: 0 !important;
}

.modal.modal-side-md .modal-content{
  height: 100%;
}


// modal fixed header and footer
.modal-fixed-foot .modal-dialog{
  height: 100%;
  position: fixed;
}
.modal-fixed-foot .HeaderTop{
  top: 0;
}
.modal-fixed-foot .modal-footer{
  bottom: 0;
  border-top:$main-border-color solid 1px ;
  box-shadow: rgba(0, 0, 0, 0.12) 0 -1px 1px 0;
}
.modal-fixed-foot .HeaderTop, .modal-fixed-foot .modal-footer{
  position: fixed;
  right: 0;
  width: 100%;
  z-index: 99;
}
.modal-fixed-foot .modal-content{
  overflow: auto;
}


.close{
  position: absolute;
  top: 0;
  right: 0;
  padding: 25px;
  z-index: 9;
}




// 6- tabs ==============================================================
.nav-tabs{
