.collapse{
  width: 100%;
}

// tabs fix
.tabs2 .tab-content>.tab-pane{
  display: block !important;
  visibility: hidden;
  height: 0;
  @include transform( scaleY(0) translateX(-50%));
}
.tabs2 .tab-content>.tab-pane.show{
  position: relative;
  visibility: visible;
  height: auto;
  @include transform( scaleY(1) translateX(0));

}

// tabs style 2
.nav-tabs2{
  -ms-flex-wrap: wrap;
  flex-wrap: nowrap;
  padding:$main-space $sm-space;
}
.nav-tabs2>li{
  margin:0 5px;
  -ms-flex: 1 1 100%;
  flex: 1 1 100%;
}
.nav-tabs2>li>a{
  border:$main-border-color solid 2px;
  color: $light-text;
  position: relative;
  text-align: center;
  border-radius: 5px;
  overflow: visible;
  display: block;
}
.nav-tabs2>li>a .nav-link{
  display: block;
  padding: $main-space $sm-space;
}
.nav-tabs2>li>a.active{
  border-color:$dark-text;
  color: $dark-text;
  background: #fff;
}

.nav-tabs2>li>a:before,
.nav-tabs2>li>a:after{
  content: '';
  position: absolute;
  left: 50%;
  z-index: 9;
}
.nav-tabs2>li>a:before{
  @include transition( opacity 0.3s ease-in-out);
  border-right: transparent solid 12px;
  border-left: transparent solid 12px;
  bottom: -12px;
  margin-left: -12px;
}
.nav-tabs2>li>a:after{
  bottom: -10px;
  margin-left: -10px;
  border-right: transparent solid 10px;
  border-left: transparent solid 10px;
}
.nav-tabs2>li>a.active:after{
  border-top: #fff solid 10px;
}
.nav-tabs2>li>a.active:before{
  border-top: $dark-text solid 12px;
}



// 5- Buttons ripple effect ==========================================================================

@mixin waves-transition($transition){
  -webkit-transition: $transition;
  -moz-transition: $transition;
  -o-transition: $transition;
  transition: $transition;
}

@mixin waves-transform($string){
  -webkit-transform: $string;
  -moz-transform: $string;
  -ms-transform: $string;
  -o-transform: $string;
  transform: $string;
}

@mixin waves-box-shadow($shadow){
  -webkit-box-shadow: $shadow;
  box-shadow: $shadow;
}

.waves-effect {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  .waves-ripple {
    position: absolute;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin-top:-50px;
    margin-left:-50px;
    opacity: 0;

    background: rgba(0,0,0,0.1);

    //$gradient: rgba(0,0,0,0.2) 0,rgba(0,0,0,.3) 40%,rgba(0,0,0,.4) 50%,rgba(0,0,0,.5) 60%,rgba(255,255,255,0) 70%;
    //background: -webkit-radial-gradient($gradient);
    //background: -o-radial-gradient($gradient);
    //background: -moz-radial-gradient($gradient);
    //background: radial-gradient($gradient);

    pointer-events: none;
  }

  &.waves-light .waves-ripple {
    background: rgba(255,255,255,0.4);

  }

  &.waves-classic .waves-ripple {
    background: rgba(0,0,0,0.2);
  }

  &.waves-classic.waves-light .waves-ripple {
    background: rgba(255,255,255,0.4);
  }
}


.waves-notransition {
  @include waves-transition(none #{"!important"});
}

.waves-button,
.waves-circle {
  @include waves-transform(translateZ(0));
  -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
}

.waves-button,
.waves-button:hover,
.waves-button:visited,
.waves-button-input {
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  border: none;
  outline: none;
  color: inherit;
  background-color: rgba(0, 0, 0, 0);
  font-size: 1em;
  line-height:1em;
  text-align: center;
  text-decoration: none;
  z-index: 1;
}

.waves-button {
  padding: 0.85em 1.1em;
  border-radius: 0.2em;
}

.waves-button-input {
  margin: 0;
  padding: 0.85em 1.1em;
}

.waves-input-wrapper {
  border-radius: 0.2em;
  vertical-align: bottom;

  &.waves-button {
    padding: 0;
  }

  .waves-button-input {
    position: relative;
    top: 0;
    left: 0;
    z-index: 1;
  }
}

.waves-circle {
  text-align: center;
  width: 2.5em;
  height: 2.5em;
  line-height: 2.5em;
  border-radius: 50%;
}

.waves-float {
  -webkit-mask-image: none;
  @include waves-box-shadow(0px 1px 1.5px 1px rgba(0, 0, 0, 0.12));
  @include waves-transition(all 300ms);

  &:active {
    @include waves-box-shadow(0px 8px 20px 1px rgba(0, 0, 0, 0.30));
  }
}

.waves-block {
  display: block;
}



// Buttons ==========================================================================

.btn-solid, .btn-borderd{
  display: inline-block;
  position: relative;
  padding: 0 $lg-space 2px $lg-space;
  height: $input-height;
  line-height: $input-height;
  border-radius: 3px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  white-space: nowrap;

}
.btn-solid, .btn-borderd{

}
/* Type */
.btn-solid.btn-round, .btn-borderd.btn-round {
  border-radius: 2000em;
}

/* Size */
.btn-solid.btn-big, .btn-borderd.btn-big {
  font-size: 1.2rem;
  padding: 0 $xl-space;
  height: $input-big-height;
  line-height: $input-big-height;
  font-weight: 600;
}
.btn-solid.btn-small, .btn-borderd.btn-small {
  padding: 0 $main-space;
  height: $input-sm-height;
  line-height: $input-sm-height;
}

.btn-borderd.btn-small {
  line-height: 27px;
}
.btn-solid.btn-mini, .btn-borderd.btn-mini {
  font-size: 12px;
  padding: 0 $sm-space;
  height: $input-xs-height;
  line-height: $input-xs-height;
}



// breadcrumb ==========================================================================
.breadcrumb-wrap{
  margin-bottom: 25px;

  .breadcrumb{
    background: none;
    margin: 0;
    padding: 0;
    display: inline-flex;
    flex-wrap: nowrap;

    .breadcrumb-item{
      white-space: nowrap;
    }
  }

}



// dropdowns ==========================================================================
//.dropdown{
//  z-index: 9;
//}
.dropdown-toggle{
  cursor: pointer;
}
.dropdown-toggle:after, .dropup .dropdown-toggle::after{
  display: none;
}
.dropdown-menu-inner{
  padding: $main-space;
}


.dropdown-menu {
  font-size: inherit;
  margin: 0;
  padding: 0;
  border: none;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.15) 0 5px 5px 0;
  border-radius: 0;
  min-width: 100%;

  overflow: hidden;

  animation: fadeinTop 500ms;
  -moz-animation: fadeinTop 500ms;
  -webkit-animation: fadeinTop 500ms;
  -o-animation: fadeinTop 500ms;

  animation-fill-mode: forwards;

}

.dropdown-item{
  padding: 0.5rem 1rem;
}
.dropdown-item.active, .dropdown-item:active{
  background-color: $primary-bg;
}

