@keyframes fadeinTop {
  0% {
    opacity: 0;
    margin-top: -30px;
    height: 70%;
  }

  100% {
    opacity: 1;
    height: auto;
    margin-top: 0;
  }
}
@keyframes fadeinBottom {
  0% {
    opacity: 0;
    margin-bottom: -50%;

  }

  100% {
    opacity: 1;
    margin-bottom:0;
  }
}





