// 6-Form Style ==========================================================================

.form-control, .ui-spinner{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  height: $input-height;
  @extend %input-style;

  @include transition( all .3s ease-in-out);

}
.form-control-small .form-control{
  height: $input-height-sm;
}
.form-control-small.form-control-icon .input-icon {
  height: $input-height-sm;
  line-height: $input-height-sm;
}

.form-control.form-control-light{
  background: $lighter-bg;
}
.form-control.form-control-InDark{
  background: rgba(255,255,255,0.1);
  color: #fff;
}

.form-control:focus{
  border-color: $dark-text !important;
}

.control-label{
  margin-bottom: 10px;
  display: inline-block;
}
.form-control.form-control-round{
  border-radius: 50px;
}
.form-control-icon.form-control-small .form-control{
  height: 38px;
}

select.form-control:not([size]):not([multiple]){
  height: $input-height;
}
.select{
  position: relative;
}
.select:after{
  pointer-events: none;
  content: "\e90c";
  font-size: 14px;
  font-family: 'marouf' !important;
  speak: none;
  font-style: normal;
  position: absolute;
  right: 15px;
  top: 50%;
  line-height: 100%;
  margin-top: -7px;
}
textarea.form-control{
  height: inherit;
}
.form-control.input-lg{
  height: $input-big-height !important;
}

.select-caption{
  position: relative;
}
.select-caption .caret{

}
.select-caption .dropdown-menu{
  width: 100%;
}
.select-caption .dropdown-menu>li>a{
  padding: 10px 15px;
}


// forms with elements

.input-group-addon{
  //background-color: $lighter-bg;
  border:$main-border-color solid 1px;
  line-height: 43px;
  padding:0 $xs-space;
  border-right-width:0;
}
.d-flex .input-group-addon{
  background: none;
  border:0;
}

.input-fly-submit{
  position: relative;
}
.input-fly-submit .btn-fly{
  background: none !important;
  border: none !important;
  position: absolute;
  top:0;
  bottom: 0;
  right: 0;
}
.input-fly-submit .img-fly{
  pointer-events: none;
  position: absolute;
  bottom: 5px;
  right: 5px;
  max-height: $input-height;
}


// forms with icons

.form-control-icon{
  position: relative;
}
.form-control-icon .form-control, .form-control-icon .select2-container--default .select2-selection--single .select2-selection__rendered{
  padding-left: 2.5rem;
}
.form-control-icon .input-icon{
  position: absolute;
  top:0;
  left:1rem;
  height: $input-height;
  line-height:  $input-height;
  pointer-events: none;
  z-index: 9;
  color: $light-text;
}
.form-control-icon .input-icon svg{
  width: 16px;
  height: 16px;
}
.form-control-icon .input-icon svg path{
  fill: $light-text;
}

.form-control-icon.form-control-small .input-icon svg{
  width: 14px;
  height: 14px;
}



// pagination pagination-main

.pagination-main{
  padding: 2px 0;
  display: inline-block;
  text-align: center;

  li{
    display: inline-block;

    &.pagination-next{
      float: right;
      padding:0 2px;
    }
  }
  li.disabled a{
    cursor: text !important;
    color: $light-text !important;
    background-color: $light-bg !important;
  }

  br{
    display: none;
  }

  .page-link{
    padding: 10px;
    margin:0 2px;
    box-shadow: none;
    outline: none;
    line-height: inherit;
    color: $dark-text;
    background-color: $light-bg;
    border: none;
    border-radius: 5px;

    &.active, &:hover{
      background: $primary-text;
      color: #fff;
      box-shadow: rgba(0,0,0,0.1) 0 0 14px;
    }
  }
}










// Form control notification
.form-control-sucsess .form-control, .form-control-sucsess .select2-container--default .select2-selection--single,
.form-control-sucsess.form-control-icon .input-icon i, .form-control-sucsess small{
  border-color: #6cb267 !important;
