}


.form-control-error .form-control, .form-control-error .select2-container--default .select2-selection--single,
.form-control-error.form-control-icon .input-icon i, .form-control-error small{
  border-color: #d75a4a !important;
  color:#d75a4a !important;
}
.form-control-icon.form-control-error .input-icon svg path{
  fill: #d75a4a;
}
.form-control-info .form-control, .form-control-info .select2-container--default .select2-selection--single,
.form-control-info.form-control-icon .input-icon i, .form-control-info small{
  border-color: #33a4ce !important;
  color:#33a4ce !important;
}
.form-control-icon.form-control-info .input-icon svg path{
  fill: #33a4ce;
}


// Custom Checkbox's

.checkbox{
  width: 100%;
}
input[type=checkbox].css-checkbox, input[type=radio].css-radio {
  position:absolute; z-index:-1000;  overflow: hidden; clip: rect(0 0 0 0); height:1px; width:1px; margin:-1px; padding:0; border:0;
}

input[type=checkbox].css-checkbox + label.css-label, input[type=radio].css-radio + label.css-radioLabel {
  display: block;
  vertical-align: middle;
  cursor: pointer;
  line-height: $input-height;
  height: $input-height;
  padding-left: $checkbox-height + $sm-space;
  margin-left: $main-space;
}
.checkbox-sm input[type=checkbox].css-checkbox + label.css-label, .checkbox-sm input[type=radio].css-radio + label.css-radioLabel{
  line-height: 25px;
  height: 25px;
}
input[type=checkbox].css-checkbox + label.css-label{
}
input[type=radio].css-radio + label.css-radioLabel{
  border-radius: 50%;
}
label.css-label, label.css-radioLabel{
  position: relative;
}
label.css-label:after, label.css-radioLabel:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -11px;
  width: $checkbox-height;
  height: $checkbox-height;
}
label.css-radioLabel,
label.css-label{
  color:$light-text;
}
label.css-radioLabel:after,
label.css-label:after{
  border:$light-text solid 2px;
}
label.css-radioLabel:after{
  border-radius: 50%;
}
label.css-label:after {
  border-radius: 3px;
}

input[type=checkbox].css-checkbox:checked + label.css-label,
input[type=radio].css-radio:checked + label.css-radioLabel{
  color: $primary-bg !important;
}
input[type=checkbox].css-checkbox:checked + label.css-label:after,
input[type=radio].css-radio:checked + label.css-radioLabel:after{
  border-color: $primary-bg !important;
}
input[type=checkbox].css-checkbox:checked + label.css-label:after{
  background-color: $primary-bg !important;
}
input[type=radio].css-radio:checked + label.css-radioLabel:after,
input[type=checkbox].css-checkbox:checked + label.css-label:after{
  background-position: center;
  background-repeat: no-repeat;
  background-size: 59%;

}
input[type=radio].css-radio:checked + label.css-radioLabel:after{
  background-image: url("data:image/svg+xml,%0A%3Csvg viewBox='0 0 16 16' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg id='Ecommerce' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Login' transform='translate(-36.000000, -504.000000)' fill='%23151A24' fill-rule='nonzero'%3E%3Cg id='Group-11' transform='translate(18.000000, 367.000000)'%3E%3Cg id='Group-2' transform='translate(7.000000, 119.000000)'%3E%3Cg id='Group' transform='translate(0.000000, 4.000000)'%3E%3Crect id='Rectangle' x='11' y='14' width='16' height='16' rx='8'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}
input[type=checkbox].css-checkbox:checked + label.css-label:after {
  background-image: url("data:image/svg+xml,%0A%3Csvg width='9px' height='9px' viewBox='0 0 9 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg id='Ecommerce' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='Login' transform='translate(-25.000000, -490.000000)' fill='%23FFFFFF' fill-rule='nonzero'%3E%3Cg id='Group-11' transform='translate(18.000000, 367.000000)'%3E%3Cg id='Group-2' transform='translate(3.000000, 119.000000)'%3E%3Cg id='Group'%3E%3Cpolygon id='' points='12.1536697 4.47687729 7.01002541 12.3277028 4.14814815 9.77521764 4.53488832 9.33046644 6.89400336 11.4575374 11.6702445 4.14814815'%3E%3C/polygon%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
}

.select-caption span, .select-caption select{
  display: inline-block;
}

// alone checkbox
.alone-checkbox .checkbox{
  padding: $main-space;
}
.alone-checkbox input[type=checkbox].css-checkbox + label.css-label, .alone-checkbox input[type=radio].css-radio + label.css-radioLabel{
  height: inherit;
  margin-left: 0;
  line-height: inherit;
}

.alone-checkbox label.css-label:after, .alone-checkbox label.css-radioLabel:after{
  top:0;
  margin-top: 2px;
}


// for ios style
input[type="checkbox"].css-switch {
  position: absolute;
  opacity: 0;
}
input[type="checkbox"].css-switch + label {
  position: relative;
  padding: 0 $main-space;
  height: $input-height;
  line-height: $input-height;
  width: 100%;
}
input[type="checkbox"].css-switch + label:before {
  content: "";
  position: absolute;
  display: block;
  right: $main-space;
  top: 50%;
  margin-top: -17px;
  width: 60px;
  height: 34px;
  border-radius: 120px;
  background: $lighter-bg;
  @include transition( all .3s ease-in-out);

}
input[type="checkbox"].css-switch + label:after {
  content: "";
  position: absolute;
  display: block;
  right: 20px;
  top: 50%;
  margin-top: -14px;
  width: 28px;
  height: 28px;
  border-radius: 60px;
  background: #fff;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.15);
  @include transition( all .3s ease-in-out);
}
input[type="checkbox"].css-switch:checked + label:after {
  margin-right: 24px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.25);
}
input[type="checkbox"].css-switch:checked + label:before {
  background: $primary-bg;
}

// for horizontal list
.option-item{
  position: relative;
  cursor: pointer;

  input{
    display: none;
  }
  label{
    cursor: pointer;
  }

  .option-item-text{
    display: block;
    padding: $sm-space;
    background: $lighter-bg;
    min-width: 36px;
    text-align: center;
  }
}

.option-item label:after{
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  top:0;
  left: 0;
  z-index: 9;
  border:transparent solid 2px;
  @include transition( all .3s ease-in-out);
}
.option-item input:checked + label:after{
