$hc-offcanvas-nav-transition-duration: .4s !default;
$hc-offcanvas-nav-transition-timing-function: ease !default;
$hc-offcanvas-nav-container-width: 500px !default;
$hc-offcanvas-nav-container-height: auto !default;

$hc-offcanvas-nav-background-color:  #fff !default;

.nav-content{
  -webkit-overflow-scrolling: auto;
}
html.hc-nav-yscroll {
  overflow-y: scroll;
}

body.hc-nav-open {
  overflow: visible;
  position: fixed;
  width: 100%;
  min-height: 100%;
}

.hc-offcanvas-nav {
  visibility: hidden;
  display: none;
  position: fixed;
  top: 0;
  height: 100%;
  z-index: 99999;

  &.is-ios {
    * {
      cursor: pointer !important;
    }
  }

  .nav-container {
    position: fixed;
    z-index: 9998;
    top: 0;
    width: 100%;
    height: 100%;
    max-width: $hc-offcanvas-nav-container-width;
    max-height: 100%;
    box-sizing: border-box;
    transition: transform $hc-offcanvas-nav-transition-duration $hc-offcanvas-nav-transition-timing-function;
  }

  .nav-wrapper {
    width: 100%;
    height: 100%;
    max-height: 100vh;
    overscroll-behavior: none;
    box-sizing: border-box;
  }

  .nav-content {
    height: 100%;
    max-height: 100vh;
  }

  .nav-wrapper-0 {
    & > .nav-content {
      overflow: scroll;
      overflow-x: visible;
      overflow-y: auto;
      box-sizing: border-box;
    }
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
    position: relative;
    display: block;

    &.level-open {
      & > .nav-wrapper {
        visibility: visible;
      }
    }
  }

  input[type="checkbox"] {
    display: none;
  }

  label {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    cursor: pointer;
  }

  a {
    position: relative;
    display: block;
    box-sizing: border-box;
    cursor: pointer;

    &,
    &:hover {
      text-decoration: none;
    }
  }

  .nav-item {
    position: relative;
    display: block;
    box-sizing: border-box;
  }

  // Background overlay

  &.disable-body,
  .nav-wrapper {
    &::after {
      content: '';
      position: fixed;
      z-index: 9990;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      overscroll-behavior: none;
      visibility: hidden;
      opacity: 0;
      transition: visibility 0s ease $hc-offcanvas-nav-transition-duration, opacity $hc-offcanvas-nav-transition-duration ease;
    }
  }

  &.disable-body.nav-open,
  .sub-level-open {
    &::after {
      visibility: visible;
      opacity: 1;
      transition-delay: .05s;
    }
  }

  &:not(.nav-open) {
    &::after {
      pointer-events: none;
    }
  }

  // Expand Levels
  &.nav-levels-expand {

    .nav-content {
      overflow: scroll;
      overflow-x: visible;
      overflow-y: auto;
      box-sizing: border-box;
    }

    .nav-wrapper {
      &::after {
        display: none;
      }
    }

    ul {
      .nav-wrapper {
        min-width: 0;
        max-height: 0;
        overflow: hidden;
        transition: height 0s ease $hc-offcanvas-nav-transition-duration;
      }
    }

    .level-open {
      & > .nav-wrapper {
        max-height: none;
      }
    }
  }

  // Transform Levels
  &.nav-levels-overlap {

    .nav-content {
      overflow: scroll;
      overflow-x: visible;
      overflow-y: auto;
      box-sizing: border-box;
    }

    ul {

      .nav-wrapper {
        position: absolute;
        z-index: 9999;
        top: 0;
        height: 100%;
        visibility: hidden;
        transition: visibility 0s ease $hc-offcanvas-nav-transition-duration, transform $hc-offcanvas-nav-transition-duration $hc-offcanvas-nav-transition-timing-function;
      }

      li {

        &.nav-parent {
          position: static;
        }

        &.level-open {
          & > .nav-wrapper {
            visibility: visible;
            transform: translate3d(0, 0, 0);
            transition: transform $hc-offcanvas-nav-transition-duration $hc-offcanvas-nav-transition-timing-function;
          }
        }
      }
    }
  }

  // Left
  &.nav-position-left {
    left: 0;

    .nav-container {
      left: 0;
      transform: translate3d(-100%, 0, 0);
    }

    &.nav-levels-overlap {

      li {
        .nav-wrapper {
          left: 0;
          transform: translate3d(-100%, 0, 0);
        }
      }
    }
  }

  // Right
  &.nav-position-right {
    right: 0;

    .nav-container {
      right: 0;
      transform: translate3d(100%, 0, 0);
    }

    &.nav-levels-overlap {

      li {
        .nav-wrapper {
          right: 0;
          transform: translate3d(100%, 0, 0);
        }
      }
    }
  }

  $hc-container-y: $hc-offcanvas-nav-container-height;
  @if $hc-offcanvas-nav-container-height == auto {
    $hc-container-y: 100%;
  }

  // Top
  &.nav-position-top {
    top: 0;

    .nav-container {
      top: 0;
      width: 100%;
      height: $hc-offcanvas-nav-container-height;
      transform: translate3d(0, -$hc-container-y, 0);
    }

    &.nav-levels-overlap {

      li {
        .nav-wrapper {
          left: 0;
          transform: translate3d(0, -100%, 0);
        }
      }
    }
  }

  // Bottom
  &.nav-position-bottom {
    top: auto;
    bottom: 0;

    .nav-container {
      top: auto;
      bottom: 0;
      width: 100%;
      height: $hc-offcanvas-nav-container-height;
      transform: translate3d(0, $hc-container-y, 0);
    }

    &.nav-levels-overlap {

      li {
        .nav-wrapper {
          left: 0;
          transform: translate3d(0, 100%, 0);
        }
      }
    }
  }

  // Open Nav
  &.nav-open[class*='hc-nav-'] {
    div.nav-container {
      transform: translate3d(0, 0, 0);
    }
  }
}


.hc-offcanvas-nav {

  &,
  .nav-wrapper {
    &::after {
      background: rgba(0, 0, 0, .3);
    }
  }

  .nav-container, .nav-wrapper {
    background: $lighter-bg;
  }
  ul {
    background: $hc-offcanvas-nav-background-color;
  }





  li {
    text-align: left;

    &.nav-parent {
      .nav-item {
        padding-right: 58px;
      }
    }

    &.nav-parent span.nav-next{
      width: 45px;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      cursor: pointer;
      transition: background .2s ease;

    }

  }

  a[href]:not([href="#"]) > span.nav-next {
    border-left: 1px solid darken($hc-offcanvas-nav-background-color, 6%);
  }

  span.nav-next,
  li.nav-back span {
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 8px;
      height: 8px;
      margin-left: -2px;
      box-sizing: border-box;
      border-top: 2px solid #333;
      border-left: 2px solid #333;
      transform-origin: center;
    }
  }

  span.nav-next {
    &::before {
      transform: translate(-50%, -50%) rotate(135deg);
    }
  }

  li.nav-back {
    span {
      &::before {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
  }

  &.nav-position-left {
    &.nav-open {
      .nav-wrapper {
        box-shadow: 1px 0 2px rgba(0, 0, 0, .2);
      }
    }
  }

  &.nav-position-right {

    &.nav-open {
      .nav-wrapper {
        box-shadow: -1px 0 2px rgba(0, 0, 0, .2);
      }
    }

    span.nav-next {
      &::before {
        margin-left: 0;
        margin-right: -2px;
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }

    li.nav-back {
      span {
        &::before {
          margin-left: 0;
          margin-right: -2px;
          transform: translate(-50%, -50%) rotate(135deg);
        }
      }
    }
  }

  &.nav-position-top {

    &.nav-open {
      .nav-wrapper {
        box-shadow: 0 1px 2px rgba(0, 0, 0, .2);
      }
    }

    span.nav-next {
      &::before {
        margin-left: 0;
        margin-right: -2px;
        transform: translate(-50%, -50%) rotate(-135deg);
      }
    }

    li.nav-back {
      span {
        &::before {
          margin-left: 0;
          margin-right: -2px;
          transform: translate(-50%, -50%) rotate(45deg);
        }
      }
    }
  }

  &.nav-position-bottom {

    &.nav-open {
      .nav-wrapper {
        box-shadow: 0 -1px 2px rgba(0, 0, 0, .2);
      }
    }

    span.nav-next {
      &::before {
        margin-left: 0;
        margin-right: -2px;
        transform: translate(-50%, -50%) rotate(45deg);
      }
    }

    li.nav-back {
      span {
        &::before {
          margin-left: 0;
          margin-right: -2px;
          transform: translate(-50%, -50%) rotate(-135deg);
        }
      }
    }
  }

  &.nav-levels-expand,
  &.nav-levels-none {

    .nav-container {

      ul {

        .nav-wrapper {
          box-shadow: none;
          background: transparent;
        }

        h2 {
          display: none;
        }

        ul {

          .nav-item {
            font-size: 14px;
          }
        }
      }

      li {
        transition: background .3s ease;

        &.level-open {
          background: darken($hc-offcanvas-nav-background-color, 4%);

          a {
            border-bottom: 1px solid darken($hc-offcanvas-nav-background-color, 8%);

            &:hover {
              background: darken($hc-offcanvas-nav-background-color, 3%);
            }
          }

          & > .nav-item {

            .nav-next {
              &::before {
                margin-top: 2px;
                transform: translate(-50%, -50%) rotate(45deg);
              }
            }
          }
        }
      }

      span.nav-next {
        &::before {
          margin-top: -2px;
          transform: translate(-50%, -50%) rotate(-135deg);
        }
      }
    }
  }
}


