// wizard tabs
.step {
  list-style: none;
  width: 100%;
  display: flex;
}

.step .step-item {
  -ms-flex: 1 1 0;
  flex: 1 1 0;
  margin-top: 0;
  min-height: 1rem;
  position: relative;
  text-align: center;
}

.step .step-item:not(:first-child):before {
  background: #54963C;
  content: "";
  height: 2px;
  left: -50%;
  position: absolute;
  top: 9px;
  width: 100%;
}

.step .step-item a {
  color: #54963C !important;
  display: inline-block;
  padding: 25px 0 0;
  text-decoration: none;
  font-weight: 600;
  position: relative;
  font-size: 13px;
}

.step .step-item a:before {
  background: #54963C;
  border: 2px solid transparent;
  border-radius: 50%;
  content: "";
  display: block;
  height: 20px;
  left: 50%;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
  width: 20px;
  z-index: 1;
}
.step .step-item a:after{
  font-size: 11px;
  font-family: 'marouf' !important;
  speak: none;
  font-style: normal;
  content: "\e935";
  left: 50%;
  position: absolute;
  top: 5px;
  transform: translateX(-50%);
  z-index: 1;
  color: #fff;
  display: block;
}
.step .step-item.active a:before {
  background: #fff;
  border: 2px solid #54963C;
}
.step .step-item.active a:after {
  width: 6px;
  height: 6px;
  content: '';
  background: #54963C;
  border-radius: 10px;
  top: 7px;
}


.step .step-item.active ~ .step-item a{
  color: $light-text !important;
}
.step .step-item.active ~ .step-item:before{
  background: $lighter-text;
}
.step .step-item.active ~ .step-item a:before {
  background: #fff;
  border-color: $lighter-text;
}
.step .step-item.active ~ .step-item a:after {
  display: none;
}


.audio-player.custom-player2 {
  position: static;
  background: #42a6ac;
}
