
// 1- Reset ==========================================================================

*{
    margin:0;
    padding:0;
    border:none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

// page loading
.loader {
  display: none;
}
body.loading .loader{
  display: table;
  position: fixed;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  z-index: 9999999;
  background-image: url("../img/loader.gif");
  background-repeat: no-repeat;
  background-position: center center;
}
.loader span{
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  padding-top: 60px;
}
.sm-text{
  font-size: 14px;
}

body.loading #MainWrapper{
  width: 100%;
  top:0;
  height: 100vh;
  overflow: hidden;
}

.inner-page #MainContent{
  padding-top:120px ;
}

.lazy{
  opacity: 0;
  visibility: hidden;
  -webkit-filter: blur(20px);
  -moz-filter: blur(20px);
  -o-filter: blur(20px);
  -ms-filter: blur(20px);
  filter: blur(20px);
}
img, svg {
  vertical-align: middle;
  max-width: 100%;
  @include transition( all 0.3s ease-in-out);
}
img:not([src]) {
  visibility: hidden;
}
@-moz-document url-prefix() {
  img:-moz-loading {
    visibility: hidden;
  }
}
hr{
  margin-top: 0;
  margin-bottom: 0;
  border-color: $main-border-color;
}
.border-t{
  border-top:$main-border-color solid 1px;
}
ul,li{
  list-style: none;
}
.flex-list{
  display: flex;
}
.flex-list-center{
  align-items: center;
}
.flex-full>*{
  flex: auto;
}
ul.flex-list>li{
  padding:0 $sm-space 0 0 !important;
  margin-bottom: 0;
}
.flex-list>li:last-child{
  padding: 0 !important;
}
.FlexList-full{
  display: flex;
}
.FlexList-full>*{
  -ms-flex: 1;
  flex: 1;
}
.d-flex-sides{
  width: 100%;
}
.d-flex-sides>*{
  text-align: right;
  margin-left: auto;
}
.d-flex-sides>*:first-child{
  text-align: left;
  margin-left: 0;
}
.d-flex-buttons>a{
  padding: $main-space $sm-space;
}
.ltr{
    direction: ltr;
}
.label-text{
  padding:0 5px;
  margin: 0 3px;
  border-radius: 50px;
}
.NotClickable{
  pointer-events: none;
}
.p-main{
  padding: $main-space;
}
.nowrap{
  white-space: nowrap;
}
article,aside,audio,canvas,command,datalist,details,embed,figcaption,figure,footer,header,hgroup,keygen,meter,nav,output,progress,section,source,video,main{
    display:block
}

a, abbr, acronym, address, applet, article, aside, audio,b, blockquote, big, body,center, canvas, caption, cite, code, command,datalist, dd, del, details, dfn, dl, div, dt, em, embed,fieldset, figcaption, figure, font, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html,i, iframe, img, ins,kbd, keygen,label, legend, li, meter,nav,object, ol, output,p, pre, progress,q, s, samp, section, small, span, source, strike, strong, sub, sup,table, tbody, tfoot, thead, th, tr, tdvideo, tt,u, ul, var{
    border: 0 none;
    font-weight: inherit;
    margin: 0;
    padding: 0;
    outline: 0;
    vertical-align: top;
}
div{
    display: block;
}
a{
    text-decoration:none;
    outline: none !important;
    color:$dark-text;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    @include transition( all .3s ease-in-out);
    cursor: pointer;
}
.link{
  color: $primary-text;
  text-decoration: underline;
}
a:hover{
