}

.HeaderBottomLight{
  background: #fff;
  border-bottom: $light-bg solid 1px;
}
.HeaderBottomLight .form-control{
  background: $lighter-bg;
}

.mainSearch-L-Ico{
  position: absolute;
  display: block;
  width: 44px;
  text-align: center;
  top: 0;
  height: 100%;
}
.IcoSearch{
  visibility: visible;
  pointer-events: none;
  font-size: 14px;
  line-height: $mainSearch-height;
  padding-top: 3px;
}
.IcoSearchClear{
  display: inline-block;
  cursor: pointer;
  font-size: 16px;
  line-height: 42px;
  right: 0;
  background: none;
  position: absolute;
  z-index: 99999;
  top: 0;
  width: 35px;
  color: $lighter-text;
  @include transition( all .3s ease-in-out);
}

.MainSearchInput:not(:valid) ~ .IcoSearchClear {
  opacity: 0;
  visibility: hidden;
}




