}

// Large devices (desktops, 992px and up)
@media (min-width: 1099px) {
  .main-brand .MobHeader-ico{
    display: none;
  }

}
@media (max-width: 1098px) {

  .bookCard{
    .book-categories{
      position: fixed;
      z-index: 99999;
    }
    .bookPage{
      padding-right: 0;
    }

  }
  .book-content:not(.bookWithoutCats){
    .bookPage .showBookCats{
      background: rgba(0, 0, 0, 0.2);
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 9999;
      border-radius: 0;
      i{
        display: none;
      }
    }
    .bookCard:after{
      display: none;
    }
  }


  .MainNav-Wrap{
    display: none;
  }

  .CategoryWithFilter .SideBar {
    width: 100%;
    max-width: 350px;
    padding:0;
    position: fixed;
    right: -100%;
    top: 0;
    background: $lighter-bg;
    z-index: 99999;
    overflow-y: scroll;
    height: 100%;
    -webkit-overflow-scrolling: touch;

    @include transition( all .3s ease-in-out);

    .box{
      border-radius: 0 !important;
    }

    .HeaderTop{
      display: block;
    }
  }
  .CategoryWithFilter.active {
    .SideBar{
      right: 0;
    }
  }

    .FilterTrigger{
    display: block;
  }
  .CategoryWithFilter.active{

    .sec-title{
      .FilterTrigger{
        background: rgba(0, 0, 0, 0.2) !important;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 9999;
        border-radius: 0;
        margin: 0 !important;
        span{
          display: none;
        }
      }
    }
  }


  .filter-foot{
    padding: 15px;

    .FilterApply{
      width: 100%;
    }

    .btn-light{
      display: none;
    }
  }



}

  // Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {

  // page directory sidebar in pc
  .Page-Sidebar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }
  .Filter-Sidebar {
    -ms-flex: 0 0 20%;
    flex: 0 0 20%;
    max-width: 20%;
  }
  .Cat-MainContent {
    -ms-flex: 0 0 80%;
    flex: 0 0 80%;
    max-width: 80%;
    padding-left: $main-space;
  }
  .CatSlider {
    margin: 15px 0 30px 0;
  }
  .Cat-MainContent #sticky-wrapper{
    display: none;
  }

  .thumbnail-items.thumbnail-items-cols > li {
    -ms-flex: 5 0 25%;
    flex: 5 0 25%;
    max-width: 25%;
    padding: 10px;
  }
  .thumbnail-items.thumbnail-items-cols.thumbnail-items-3cols > li {
    -ms-flex: 5 0 33.33%;
    flex: 5 0 33.33%;
    max-width: 33.33%;
    padding: 10px;
  }



}
