// Variables
// --------------------


// Fonts
$font-stack: 'Roboto', sans-serif;
$font-stack-ar: 'cairo', sans-serif !important;


// Colors ==================================================================
$primary-text: #42A6AC;
$alt-text: #8F1010;
$dark-text: #151A24;
$light-text: #666666;
$lighter-text: #B7BABD;

$primary-bg: #42A6AC;
$alt-bg: #8F1010;
$dark-bg: #424242;
$light-bg: #F2F0E8;
$lighter-bg: #FBF9F2;
$white-bg: #fff;

$main-border-color: #E1E6EA;




// Main Spaces
$xl-space:    25px;
$lg-space:    20px;
$main-space:  15px;
$sm-space:    10px;
$xs-space:    7px;


// Header Height's
$header-height: 52px;
$mainSearch-height: 42px;


// transitions
@mixin transition($property) {
  -webkit-transition: $property;
  -ms-transition: $property;
  transition: $property;
}
@mixin transform($property) {
  -webkit-transform: $property;
  -moz-transform: $property;
  -ms-transform: $property;
  transform: $property;
}


// Icon Thumbs sizes 'the ratio of the width to the height of an Icons'
$xxs-thumb:   25px;
$xs-thumb:    32px;
$sm-thumb:    40px;
$md-thumb:    50px;
$lg-thumb:    65px;
$xl-thumb:    85px;
$xxl-thumb:   100px;


// Images thumbnails Sizes "the ratio of the width to the height of an image"
$thumbnail-main:    130%;
$thumbnail-alt:     45%;

//--main Slider
$mainSlider-img:     40%;
$mainSlider2-img:    60%;
$mainSlider3-img:    22%;
$CatSlider-img:      25%;
$MainSlider-sideAd1: 80%;
$MainSlider-sideAd2: 42%;

// other thumbs
$thumbnail-h1:  100%;
$thumbnail-h2:  80%;
$thumbnail-h3:  70%;
$thumbnail-h4:  50%;
$thumbnail-h5:  40%;
$thumbnail-h6:  30%;

// width Of the image at the side listing "before swishing to columns"
$side-list-thumb-width: 90px;


// Font sizes
body, p{font-size: 14px;}
h1,.h1{ font-size: 34px;}
h2,.h2{ font-size: 22px;}
h3,.h3{ font-size: 20px;}
h4,.h4{ font-size: 18px;}
h5,.h5{ font-size: 16px;}
h6,.h6{ font-size: 14px;}


small{ font-size: 12px;}
strong, .strong{font-weight: 600}

body{
  font-family: $font-stack !important;
  background: $lighter-bg url("../img/bg_pattern.png");
  line-height: normal;
}
body.inner-page{
  background: #f9f8f5 url("../img/bg_pattern.png") repeat;
}
.sec-container{
  max-width: 1450px;
  padding-right: 15px;
  padding-left: 15px;
}

.sec-sm-container{
  max-width: 750px;
}
.sec-xs-container{
  max-width: 450px;
  position: relative;
}

$box-radius: 5px;

%box{
  background: rgba(255,255,255,0.93);
  border: 1px solid #E6E6E6;
  box-shadow: 0 4px 5px 0 rgba(0,0,0,0.06);
  border-radius: 0;
}


// Form styles
$input-big-height:  55px;
$input-height:      45px;
$input-height-sm:   38px;
$input-sm-height:   30px;
$input-xs-height:   24px;

$checkbox-height: 22px;

%input-style {
  border-radius: 0;
  outline: none;
  width: 100%;
  background: #fff;
  box-shadow: none;
  border:1px solid $main-border-color !important;
}
