html[dir="rtl"]{

  body{
    direction: rtl;
    text-align: right;
    font-family: $font-stack-ar;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.1rem;
  }

  .dropdown-menu {
    text-align: right;
  }

  .list-items.list-items-md > li > .list-item:after {
    left: 0;
    right: 30px;
    margin-right: 50px;
    margin-left: 0;
  }

  .pagination-main li.pagination-next {
    float: left;
  }

  .ml-auto{
    margin-right: auto!important;
    margin-left: inherit !important;
  }

  header .dropdown-menu{
    text-align: right;
    left: inherit !important;
    right: 0 !important;
  }

  .md-menu .dropdown-menu {
    left: 0 !important;
    right: auto !important;
  }

  .side-thumbnail-items .thumb-body, .list-item-body {
    padding-left: 0;
    padding-right: $main-space;
  }

  .list-items .list-item:after {
    left: 0;
    right: 15px;
  }
  .list-items.list-items-xs > li > .list-item:after {
    right: 30px;
    margin-right: 32px;
    left: 0;
    margin-left: 0;
  }

  .secTitleR .ico-angle-right:before{
    content: "\e90d";
  }

  .main-brand .MobHeader-ico{
    margin-left: 15px;
  }

  .fly-buttons {
    left: 10px;
    right: inherit !important;
  }

  // side menu
  .hc-offcanvas-nav li {
    text-align: right;
  }
  .MainNav-Title, .hc-offcanvas-nav h2 {
    padding: 55px 15px 15px 0;
  }
  .hc-offcanvas-nav li.nav-parent span.nav-next {
    left: 0;
    right: auto !important;
  }
  .hc-offcanvas-nav li.nav-parent .nav-item {
    padding-right: $main-space;
  }
  .MainNav-Center a:after, .MainNav-Center .nav-item:after{
    left: 0;
    right: $main-space;
  }

  .list-item-side {
    padding-left: 20px;
    padding-right: 0;

    .ico-rightArrow:before{
      content: "\ea6a";
    }
  }

  .MainAlert-s2.list-item .list-item-head{
    margin-left: 0;
    margin-right: -15px;
  }

  .nav-back a span {
    right: 15px;
    left: auto !important;
  }

  .form-control-icon .input-icon {
    left: auto !important;
    right: 1.1rem;
  }

  .form-control-icon .form-control, .form-control-icon .select2-container--default .select2-selection--single .select2-selection__rendered {
    padding-left: 0 !important;
    padding-right: 2.5rem;
  }

  .nav-md .nav-item > a {
    font-weight: 600;
  }

  .mega-side-nav .nav li a, .strong5{
    font-weight: 600 !important;
  }
  .md-menu .dropdown-menu .h5 {
    font-size: 14px;
  }


  .price a{
    left: 0;
    right: inherit;
  }

  .header-pup {
    margin-right: 25px;
    margin-left: 0;
  }
  .header-pup:before {
    left: inherit;
    right: -10px;
    border-right: transparent solid 10px;
    border-left:none;
  }

  .MobHeader-noti {
    top: 5px;
    right: 7px;
    left: inherit;
  }

  .price-msg:after {
    right: -3px;
    left: inherit;
    border-right: transparent solid 3px;
    border-left: none;
  }
  .price-new{
    line-height: 1.8rem;
  }
  .IcoSearchClear{
    left: 0;
    right: auto;
  }

  .ui-autocomplete li.ui-menu-item {
    padding-right: 50px;
    padding-left: 0;

    &:after{
      right: 15px;
      left: auto;
    }
  }

  .input-group-addon{
    border-right-width:1px !important;
    border-left-width:0;
  }

  .ios .backFromSearch, .close, .input-fly-submit .btn-fly {
    left: 0;
    right: auto;
  }
  .android .backFromSearch {
    left: auto;
    right: 10px;

    .ico-arrow-left:before{
      content: "\e919";
    }
  }

  .mainMenuTrigger .ico-Menu:before{
    content: "\ea9d";
  }

  .d-flex-sides > * {
    text-align: left;
    margin-right: auto;
    margin-left: 0;

    &:first-child {
      text-align: right;
      margin-right: 0;
    }
  }


  .stars, .percentage{
    background-position: center right;
  }
  .stars{
    margin-right: 0;
    margin-left: 4px;
  }
  .ratting strong {
    line-height: 0.85rem;
  }


  /* book cover */
  .book-cover {
    padding-left:0;
    padding-right: 5px;
  }
  .book-cover img{
    left: 0;
    right: inherit;
  }
  .book-cover:before{
    background: -webkit-linear-gradient(right, rgba(0,0,0,0.4) , rgba(0,0,0,0));
    background: -o-linear-gradient(right, rgba(0,0,0,0.4) , rgba(0,0,0,0));
    background: -moz-linear-gradient(right, rgba(0,0,0,0.4) , rgba(0,0,0,0));
    background: linear-gradient(right, rgba(0,0,0,0.4) , rgba(0,0,0,0));

    left:inherit;
    right: 0;
  }
  .book-cover:after{

    border-top-right-radius: 140px;
    border-bottom-right-radius: 160px;

    border-top-left-radius: 0;
    border-bottom-left-radius: 0;

    left: 6%;
    right: 4%;
  }
  .book-cover-mark{
    right: 7%;
    left: inherit;
    background: url(../img/book-cover-mark.svg) bottom right no-repeat;
  }
  .book-cover--big .book-cover:after{
    left: 4%;
    right: 3%;
  }

  .book-parts-patch{
    left: -4px;
    right: -3px;
  }
  .book-parts-patch span{
    margin-left: 0;
    margin-right: 18px;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 0;
  }
  .book-info {
    left: -5px;
    right:inherit;
  }


  .text-icon > i {
    margin-right: 0;
    margin-left: 0.5rem;
  }

  .text-icon {
    padding-left: $main-space;
    padding-right: 0;
  }
  .box-foot-btn{
    text-align: left;
  }


  .breadcrumb-item+.breadcrumb-item {
    padding-right: .5rem;
    padding-left:0;
  }

  .breadcrumb-item+.breadcrumb-item:before {
    padding-left: .5rem;
    padding-right: 0;
  }



  .multiLevels-menu li .mlItem-expand, .multiLevels-menu li .ml-item-ico {
    left: inherit;
    right: 0;
  }
  .multiLevels-menu li a {
    padding: 0.5rem 2.8rem 0.8rem 1rem;
  }
  .side-menu .multiLevels-menu>ul {
    padding-left: 0;
    padding-right: 1rem;
  }
  .multiLevels-menu ul li ul {
    border-right: 1px solid #D9DADB;
    border-left: 0;
    margin: 0 12px 0 0;
    padding: 0 1rem 0 0;
  }
  .multiLevels-menu ul li ul li:before {
    right: -1rem;
  }


  input[type=checkbox].css-checkbox + label.css-label, input[type=radio].css-radio + label.css-radioLabel {
    padding-right: 32px;
    margin-right: 15px;
    padding-left: 15px;
    margin-left: 0;
    text-align: right;
  }
  label.css-label:after, label.css-radioLabel:after {
    right: 0;
    left: inherit;
  }


  .list-items.list-items-sm > li > .list-item:after {
    right: 30px;
    margin-right: 40px;
    left: 0;
    margin-left: 0;
  }

  .list-icons > li{
    padding: 0 0 0 15px;
  }


}



@media (min-width: 992px){
  html[dir="rtl"]{
    .offset-lg-1 {
      margin-left: 0 !important;
      margin-right: 8.333333%;
    }
  }

}


@media (max-width: 768px) {
  html[dir="rtl"] {
    .ios #mainSearch {
      padding-right: $main-space !important;
      padding-left: 72px !important;
    }

    .android #mainSearch {
      padding-left: $main-space !important;
      padding-right: 60px !important;
    }

  }
}

@media (min-width: 768px) {
  html[dir="rtl"] {
    .offset-md-3 {
      margin-left: 0;
      margin-right: 25%;
    }
  }
}



@media (min-width: 576px) {
  html[dir="rtl"] {

    .offset-sm-3 {
      margin-right: 25%;
      margin-left: 0;
    }
  }
}

